@charset "UTF-8";
.contact {
  box-sizing: border-box;
}

.contact li {
  list-style: none;
}

.contact .sp-br {
  display: none;
}

.contact .contact__content .contact__content__title {
  font-size: 3.2rem;
  font-weight: 700;
  position: relative;
  text-align: center;
  margin-bottom: 50px;
}

.contact .contact__content .contact__content__title:after {
  position: absolute;
  bottom: calc(50% - 7px);
  left: 0;
  width: 100%;
  height: 10px;
  content: '';
  background-image: -webkit-repeating-linear-gradient(135deg, #e45213, #e45213 1px, transparent 2px, transparent 5px);
  background-image: repeating-linear-gradient(-45deg, #e45213, #e45213 1px, transparent 2px, transparent 5px);
  background-size: 7px 7px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.contact .contact__content .contact__content__title span {
  padding: 0 25px;
  background: #fff;
  position: relative;
  z-index: 10;
}

.contact .contact__content .contact__content__sub-title {
  font-size: 2.4rem;
  color: #fff;
  background: #E45213;
  border-left: solid 6px #ff8748;
  padding: 13px 20px;
  margin-bottom: 25px;
  font-weight: 400;
}

.contact .contact__content .contact__form {
  color: #333;
  line-height: 22.4px;
}

.contact .contact__content .contact__form th, .contact .contact__content .contact__form td {
  padding: 12px;
  text-align: left;
}

.contact .contact__content .contact__form th {
  font-size: 1.6rem;
  background: #70C438;
  color: #fff;
  font-weight: bold;
  width: 20%;
  border: 1px solid #70C438;
  border-right-color: #fff;
  border-bottom-color: #fff;
}

.contact .contact__content .contact__form th .contact__th__caution {
  font-size: 1.4rem;
  color: #fff;
}

.contact .contact__content .contact__form th .mandatory {
  color: #fff;
  position: relative;
}

.contact .contact__content .contact__form th .mandatory:after {
  content: "必須";
  font-size: 1.1rem;
  line-height: 1;
  color: #fff;
  padding: 2px;
  background: #E45213;
  position: absolute;
  right: -30px;
  bottom: 2px;
  border-radius: 2px;
}

.contact .contact__content .contact__form td {
  width: 80%;
  border: solid 1px #cdcdcd;
  border-radius: 2px;
  font-size: 1.4rem;
}

.contact .contact__content .contact__form td .contact__form__caution {
  color: #E45213;
}

.contact .contact__content .contact__form td .contact__form__caution:nth-child(n+2) {
  margin-top: 8px;
}

.contact .contact__content .contact__form td input[type="text"], .contact .contact__content .contact__form td input[type="email"] {
  padding: 5px;
  border: solid 1px #767676;
  width: 100%;
  color: #333;
  line-height: 19.6px;
}

.contact .contact__content .contact__form td input[type="text"]:hover, .contact .contact__content .contact__form td input[type="email"]:hover {
  border: solid 1px #000;
}

.contact .contact__content .contact__form td input[type="text"]:focus, .contact .contact__content .contact__form td input[type="email"]:focus, .contact .contact__content .contact__form td textarea:focus {
  border: solid 1px #000;
}

.contact .contact__content .contact__form td select {
  text-align: center;
}

.contact .contact__content .contact__form td .mwform-radio-field-text,
.contact .contact__content .contact__form td .mwform-checkbox-field-text {
  padding-right: 0;
  line-height: 22.4px;
}

.contact .contact__content .contact__form td label {
  border: dotted 1px #FFCE3C;
  border-radius: 3px;
  padding: 8px 6px 6px;
  margin: 5px 10px 5px 0;
  line-height: 22.4px;
}

.contact .contact__content .contact__form td label:hover {
  background: #FFCE3C;
}

.contact .contact__content .contact__form td label.selected {
  background: #FFCE3C;
}

.contact .contact__content .contact__form td input[type="radio"] + span::before {
  width: 13px;
  height: 13px;
  border: solid 1px #767676;
  border-radius: 50%;
}

.contact .contact__content .contact__form td input[type="radio"] + span:hover::after {
  background: #fff;
}

.contact .contact__content .contact__form td input[type="radio"]:checked + span::before {
  border: solid 1px #2C77F6;
}

.contact .contact__content .contact__form td input[type="radio"]:checked + span::after {
  background: #2C77F6;
  width: 9px;
  height: 9px;
  left: -4.5px;
  margin-top: -5px;
  z-index: 10;
}

.contact .contact__content .contact__form td input[type="radio"]:checked + .mwform-radio-field-text {
  color: #333;
}

.contact .contact__content .contact__form td input[type="checkbox"] + span::before {
  width: 13px;
  height: 13px;
  border: solid 1px #767676;
  border-radius: 3px;
}

.contact .contact__content .contact__form td input[type="radio"] + span:hover::before,
.contact .contact__content .contact__form td input[type="checkbox"] + span:hover::before {
  border-color: #333;
}

.contact .contact__content .contact__form td input[type="checkbox"] + span:hover::after {
  background: rgba(255, 255, 255, 0);
}

.contact .contact__content .contact__form td input[type="checkbox"]:checked + span::before {
  border: solid 1px #2C77F6;
  background: #2C77F6;
}

.contact .contact__content .contact__form td input[type="checkbox"]:checked + span::after {
  content: "";
  position: absolute;
  width: 7.5px;
  height: 4px;
  margin-top: -5px;
  top: 50%;
  left: -4.5px;
  transform: rotate(-45deg);
  border-bottom: 2px solid #fff;
  border-left: 2px solid #fff;
  background: #2C77F6;
}

.contact .contact__content .contact__form td input[type="checkbox"]:checked + .mwform-checkbox-field-text {
  color: #333;
}

.contact .contact__content .contact__form td .contact__form__visit {
  margin-bottom: 8px;
}

.contact .contact__content .contact__form td textarea {
  vertical-align: top;
}

.contact .contact__content .contact__form td .error {
  font-size: 1.3rem;
  color: #B70000;
  display: block;
}

.contact .contact__content .contact__form .confirm-btn {
  margin: 20px 0;
  text-align: center;
}

.contact .contact__content .contact__form .confirm-btn input[type="submit"] {
  font-size: 1.8rem;
  letter-spacing: 0.1rem;
  color: #fff;
  background: #EB5E00;
  font-family: noto-sans-cjk-jp,sans-serif;
  font-weight: 600;
  text-align: center;
  align-items: center;
  border: solid 1px #EB5E00;
  width: 290px;
  height: 60px;
}

@media screen and (max-width: 767px) {
  .contact .sp-br {
    display: block;
  }
  .contact .contact__content .contact__content__title {
    display: none;
  }
  .contact .contact__content .contact__content__title span {
    display: none;
  }
  .contact .contact__content .contact__content__sub-title {
    font-size: 2rem;
  }
  .contact .contact__content .contact__form th {
    display: block;
    width: 100%;
  }
  .contact .contact__content .contact__form td {
    display: block;
    width: 100%;
  }
  .contact .contact__content .contact__form td .error {
    font-size: 1.2rem;
  }
  .contact .contact__content .contact__form .confirm-btn input[type="submit"] {
    width: 200px;
    height: 60px;
  }
}

.mw_wp_form_complete p {
  font-size: 18px;
  text-align: center;
}
